import React, { useState, useEffect} from 'react';
import { SettingStore } from '../stores';

export default ()=>{
  const [ sts, setSts] = useState(false);

  useEffect(()=>{
    const handleSubscribe = SettingStore.subscribe(() => {
      if (SettingStore.getState()._watcher === 'popupTrigger') {
         setSts(!SettingStore.getState().popup ? false : true);
      }
    });
    return ()=> {
      handleSubscribe();
    }

  },[]);
  return (<span>
    <div className={(sts) ? 'popup-on' : 'popup-off'}></div>
    <div className={(sts) ? 'popup-body' : 'popup-off'}>
      {SettingStore.getState().popup}
    </div>
  </span>
  )
}
